import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View , Alert} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			activeTab6: 'all',
			priv: null,
			num: localStorage.getItem('num') ? Number(localStorage.getItem('num')) : 0,
			rating: localStorage.getItem('rating') ? Number(localStorage.getItem('rating')) : 0,
			popout: null,
		};
		this.closePopout = this.closePopout.bind(this);
	}

	openPopout = () => {
		this.setState({ popout:
		  <Alert
			actions={[{
			  title: 'Ок',
			  autoclose: true,
			  style: 'destructive'
			}]}
			onClose={this.closePopout}
		  >
			<h2>Ошибка!</h2>
			<p>Недостаточно денег!
            1 рейтинг стоит 50.000</p>
		  </Alert>
		});
	  }
	  closePopout = () => {
		this.setState({ popout: null });
	  }

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
					case 'VKWebAppShowWallPostBoxResult':

  					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	}

	add1 = () => {
		this.setState({num: this.state.num+1})
		localStorage.setItem('num', this.state.num+1);
	}

	add2 = () => { 
		if (this.state.num< 50000) {this.openPopout()} 
		else {
			this.setState({rating: this.state.rating+1, num: this.state.num-50000})
			localStorage.setItem('rating', this.state.rating + 1);
			localStorage.setItem('num', this.state.num - 50000);
		}
	}

	render() {
		return (
			<View popout={this.state.popout} activePanel={this.state.activePanel}>
				<Home id="home" add1={this.add1} add2={this.add2} num={this.state.num} rating={this.state.rating} priv={this.state.priv} fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
